import { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import './favoritos.css'
import { toast } from 'react-toastify'

export default function Favoritos () {
    const [filmes, setFilmes] = useState([])

    // Quando a pgina carregar, pegaremos todos os filmes que salvamos em localStorage e listaremos
    useEffect(() => {
        const minhaLista = localStorage.getItem('filmes')
        setFilmes(JSON.parse(minhaLista) || []) // Se no houver algo no localStorage, ento passamos um array vazio
    }, [])

    // Funo de deletar um item da nossa lista (deletar no localStorage)
    function handleDelete(idFilmeDeletado) {
        // Iremos retornar a lista de todos os filmes que esto no localStorage, menos aquele que foi clicado pra deletar
        // Vamos usar o filter, do JS Vanilla, para percorrer nossa lista (e os objetos dentro dela) de filmes retornada (porm sem o que iremos deletar)
        let filtroFilmes = filmes.filter((item) => {
            // Retornaremos todos os objetos que tem o id diferente do que clicamos
            return (item.id !== idFilmeDeletado)
        })
        
        // Agora setaremos os filmes no nosso estado e no localStorage, menos aquele filme que exclumos
        setFilmes(filtroFilmes);
        localStorage.setItem('filmes', JSON.stringify(filtroFilmes))

        toast.success('Filme Excludo com Sucesso !') // Usando a biblioteca de alertas
    }

    return (
        <div id="meus-filmes" className='meus-filmes'>
          <h1>Filmes Salvos</h1>

            { /* Se a quantidade de itens no nosso array for igual a zero, quer dizer que no temos filmes na lista */ }
            {filmes.length === 0 && <span><p>Voc no possui filme salvo :( </p></span>}

            <ul className='lista-de-salvos'>
              {filmes.map((item) => {
                return (
                  <li key={item.id}>
                    <span>{item.nome}</span>
                      <div className='detalhes-salvos'>
                        <Link className='detalheFilm' to={`/filme/${item.id}`}>Detalhes</Link>
                        { /*  importante chamar o mtodo abaixo, a partir de uma funo annima. Do contrrio apagar tudo */ }
                        { /* ... Pois com funo annima a funo ali no  executada. Apenas quando ns clicarmos */ }
                        { /* ... Fazemos isso pq estamos passando parmetro */ }
                        <button onClick={ () =>  handleDelete(item.id) } className="btn-remover">Excluir</button>
                      </div>
                  </li>
                )
              })}
            </ul>
        </div>
    )
}